
import { Injectable } from '@angular/core';
import { AngularFirestore } from 'angularfire2/firestore';
import * as firebase from 'firebase/app';
import { ActivatedRouteSnapshot, Resolve, RouterStateSnapshot } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';

@Injectable()
export class UserService implements Resolve<any> {

    users: any[];
    onUsersChanged: BehaviorSubject<any> = new BehaviorSubject({});

    constructor( private afs: AngularFirestore ) { }

    /**
     * Resolve
     * @param {ActivatedRouteSnapshot} route
     * @param {RouterStateSnapshot} state
     * @returns {Observable<any> | Promise<any> | any}
     */
    resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<any> | Promise<any> | any {
        return this.getUsers();
    }

    getUsers(): Promise<any> {
        return new Promise((resolve, reject) => {
          this.afs.collection('users').snapshotChanges().map(changes => {
            return changes.map(c => ({id: c.payload.doc.id, ...c.payload.doc.data()}));
          }).subscribe(users => {
            this.users = users;
            this.onUsersChanged.next(this.users);
            resolve(users);
          }, reject);
        });
    }

    unBlockUser(user: any) {
        return this.afs.doc(`users/${user.id}`).update({
            isBlocked: false
        });
    }

    deleteUser(user: any) {
        return this.afs.doc(`users/${user.id}`).delete();
    }

}



